import { lazy } from "react";
import IntroContent from "../../content/IntroContent.json";
import ExperinceContent from "../../content/AboutContent.json";
import ProductContent from "../../content/ProductContent.json";

const Container = lazy(() => import("../../common/Container"));
const ScrollToTop = lazy(() => import("../../common/ScrollToTop"));
const ContentBlock = lazy(() => import("../../components/ContentBlock"));

const Home = () => {
  return (
    <Container>
      <ScrollToTop />
      <ContentBlock
        type="right"
        title={IntroContent.title}
        content={IntroContent.text}
        button={IntroContent.button}
        icon="undraw_Web_developer_re_h7ie.svg"
        id="about"
      />
      <ContentBlock
        type="left"
        title={ExperinceContent.title}
        content={ExperinceContent.text}
        section={ExperinceContent.section}
        icon="undraw_chore_list_re_2lq8.svg"
        id="experience"
      />
      <ContentBlock
        type="products"
        title="products"
        content={ProductContent}
        icon="undraw_dev_productivity_umsq.svg"
        id="product"
      />
    </Container>
  );
};

export default Home;
